@use "../../../../styles//tools//truncate.scss" as textTransf;
@import "../../../../styles//breakpoints.scss";
.host {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 20px; 
    margin-bottom: 30px;    
    @media (--app-viewport-medium-max) {
        flex-direction: column;
    }
}
.buttonStyle {
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    border: 1px solid #d2d2d2;
}

.borderedCardHost {
    flex: 1; 
    background-color: var(--app-color-white-lightest);
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-height: 283px;
}
.borderedCardContainer {
    padding: 30px;
    //border: 1px solid var(--app-color-gray-lighter);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    min-width: 319px;

    p {
        font-family: var(--app-text-main-font);
        font-size: 1rem;
        line-height: 1.5rem;
        color: var(--app-color-gray-dark);
    }
}
.unBorderedCardOrgChartHost {
    position: relative;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    width: 100%;
    min-height: 282px;
    //max-height: 300px;
    background-color: var(--app-color-white-lightest);
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    padding: 16px 24px;
    margin-right: 20px;

    
}
.unBorderedCardLoadingContainer {
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.bottomStatisticsOrgChartHost {
    display: flex;
    padding-top: 10px;
    align-self: flex-end;
    // z-index: 1;
    width: 100%;
    gap: 10px;
}
.bottomStatisticsOrgChart {
    display: flex;
    flex: 1;
    justify-content: space-around;
    margin-bottom: -5px;
    p {
        color: var(--app-color-gray-dark);
        font-size: 16px;
        margin: 0;
        padding: 0;
    }
}
.unBorderedCardOverviewHost {
    position: relative;
    border: none;
    outline: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    width: 100%; 
    min-height: 282px;
    max-height: 300px;
    background-color: var(--app-color-white-lightest);
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    padding: 16px 24px;
    margin-right: 20px;
}
.companyOverviewRow {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
}
.companyOverviewNameContainer {
    @include textTransf.textEllipsed(2);
    flex: 1;
    align-items: center;
}
.companyOverviewNameText {
    width: auto;
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    text-align: left;
    color: var(--app-color-gray-dark);
}
.companyOverviewLogoContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background-color: var(--app-color-gray-lighter_nuance2);
}
.companyOverviewLogo {
    object-fit: contain;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    padding: 1px;
}
.companyOverviewLogoFallback {
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    color: var(--app-color-gray-light);
}
.companyOverviewLabeledContainer {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}
.companyOverviewLabel {
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    color: var(--app-color-gray-light_nuance);
}
.companyOverviewDataContainer {
    @include textTransf.textEllipsed(4);
}
.companyOverviewData {
    font-family:var(--app-text-main-font);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: var(--app-color-gray-dark);
    margin: 0;
    padding: 0;
}
/* endregion General error */
.labeledContainerHost {
}
.labeledContainerLabel {
    font-family: var(--app-text-main-font);
    font-size: 24px;
    color: #4a4a4a;
}
