.companyHost {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 8% 0 8%;
}

.usersHost {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 12% 0 8%;
}

.bottomText {
  display: flex;
  flex-direction: row;
  margin-top: -30px;

  p {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    line-height: 1.3rem;
    color: var(--app-color-gray-light);
  }
}

.topText {
  display: flex;
  flex-direction: row;

  p {
    font-size: 1.5rem;
    line-height: 1.8rem;
    color: var(--app-color-gray-dark);
  }
}

.textSection {
  display: flex;
  flex-direction: column;
  padding-left: 40px;
}

.noColleaguesBackground {
  background-color: #f1f1f1;
  border-radius: 8px;
  padding: 20px;
}

.noColleaguesContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.noColleaguesInnerContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.noColleaguesSection {
  padding-top: 30px;
  padding-bottom: 50px;
}

.bottomOrgTextContainer {
  display: flex;
  flex: 1;
  justify-content: center;
}

.orgChartContainer {
  padding: 30px;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  p {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    line-height: 1.5rem;
    color: #4a4a4a;
  }
}

.orgChartSection {
  max-width: 550px;
  background-color: var(--app-color-white-lightest);
  border-radius: 8px;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.noTeamMembers {
  display: flex;
  flex: 2;
  flex-direction: column;
  padding-bottom: 100px;
  justify-content: center;
  text-align: center;
  align-items: center;

  p {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    line-height: 1.2rem;
    color: var(--app-color-gray-light);
  }
}

.colleaguesSection {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-bottom: 100px;
}

.leftSideContainer {
  display: flex;
  flex-direction: column;
  padding-right: 50px;
  min-width: 240px;

  p {
    font-family: var(--app-text-main-font);
    font-size: 1.5rem;
    line-height: 1.5rem;
    color: var(--app-color-gray-light);
  }
}

.rightSideContainer {
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-wrap: wrap;
  align-content: flex-start;
  //position: relative;
}

.loadingIndicator {
  position: absolute;
  left: -35px;
  top: 5px;
}

.filterButton {
  width: 100%;

  p {
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: bold;
    font-size: 16px;

    color: var(--app-color-white-lightest);
  }
}

.filterButtonIcon {
  width: 22px;
  height: 24px;
}

.resetFilterButton {
  border: 1px solid var(--app-color-gray-dark);
  height: 40px;
  margin: auto 5%;

  p {
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    padding: 0;
    margin: 0;
    margin-bottom: 5px;
    color: var(--app-color-gray-dark);
  }
}

.resetFilterButtonIcon {
  width: 22px;
  height: 16px;
}

.navbarSection {
  padding: 0 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
}

.logoUrlImg {
  object-fit: scale-down;
  height: 100px;
  width: 250px;
  margin: 5px;
}

.logoUrlImgM {
  object-fit: scale-down;
  height: 80px;
  width: 200px;
  margin: 5px;
}
.banner {
  width: 100%;
  height: auto;
  max-width: 550px;
  background-color: orange;
  color: white;
  border-radius: 8px;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
  padding: 50px;
  margin: 30px 0 0 0;
}
.banner2 {
  width: 100%;
  height: auto;
  max-width: 550px;
  background-color: white;
  // color: white;
  border-radius: 8px;
  box-shadow: 10px 20px 20px 0 rgba(0, 0, 0, 0.1);
  padding: 50px;
  margin: 30px 0 0 0;
}
.companyHome {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 10px 0 30px;
  //background: linear-gradient(0deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3)), url('../../../assets/images/office.png');
}

.centerDiv {
  font-weight: lighter;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 10px;
  color: var(--app-color-gray-dark);
}